"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { ChevronDown, Copy, RefreshCw, CheckCircle2, ArrowLeft } from "lucide-react";
import { useCharacter } from "@/context/CharacterContext";
import Link from "next/link";

export default function MultipleCharactersResultPage() {
  const { characters } = useCharacter();
  const [expandedCards, setExpandedCards] = useState<Set<string>>(new Set([characters[0]?.id]));
  const [copied, setCopied] = useState<string | null>(null);

  const toggleCard = (characterId: string) => {
    setExpandedCards((prev) => {
      const next = new Set(prev);
      if (next.has(characterId)) {
        next.delete(characterId);
      } else {
        next.add(characterId);
      }
      return next;
    });
  };

  const copyToClipboard = (text: string, section: string) => {
    navigator.clipboard.writeText(text);
    setCopied(section);
    setTimeout(() => setCopied(null), 2000);
  };

  if (characters.length === 0) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center px-4">
        <div className="text-center">
          <p className="text-xl font-semibold text-foreground mb-4">No characters found</p>
          <Link
            href="/create"
            className="inline-block px-6 py-3 rounded-xl bg-violet-500 hover:bg-violet-600 text-white font-medium transition-colors"
          >
            Create Characters
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background px-4 py-8">
      <div className="max-w-5xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <Link
            href="/wizard"
            className="inline-flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors mb-6"
          >
            <ArrowLeft className="w-5 h-5" />
            <span className="text-sm">Back to Wizard</span>
          </Link>
          <h1 className="text-4xl font-bold text-foreground mb-2">
            Character Results ({characters.length})
          </h1>
          <p className="text-muted-foreground">
            Review and edit your generated characters
          </p>
        </div>

        {/* Character Cards */}
        <div className="space-y-4">
          {characters.map((character, index) => {
            const isExpanded = expandedCards.has(character.id);
            const generated = character.generatedContent || {
              personality: "Personality content will be generated here...",
              backstory: "Backstory content will be generated here...",
              traits: "Core traits will be generated here...",
              speech: "Speech mannerisms will be generated here...",
              initialMessage: "Initial message will be generated here...",
            };

            const sections = [
              { id: "personality", title: "Personality", content: generated.personality },
              { id: "backstory", title: "Backstory", content: generated.backstory },
              { id: "traits", title: "Core Traits", content: generated.traits },
              { id: "speech", title: "Speech Mannerisms", content: generated.speech },
              { id: "initialMessage", title: "Initial Message", content: generated.initialMessage },
            ];

            return (
              <motion.div
                key={character.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
                className="glass rounded-2xl border border-border overflow-hidden"
              >
                {/* Card Header */}
                <button
                  onClick={() => toggleCard(character.id)}
                  className="w-full p-6 flex items-center justify-between hover:bg-white/5 transition-colors"
                >
                  <div className="flex items-center gap-4">
                    <div className="w-12 h-12 rounded-xl bg-violet-500/20 flex items-center justify-center">
                      <span className="text-xl font-bold text-violet-400">{index + 1}</span>
                    </div>
                    <div className="text-left">
                      <h3 className="text-xl font-semibold text-foreground">
                        {character.basics.name || `Character ${index + 1}`}
                      </h3>
                      <p className="text-sm text-muted-foreground">
                        {character.basics.setting} â€¢ {character.basics.age} years old
                      </p>
                    </div>
                  </div>
                  <ChevronDown
                    className={`w-5 h-5 text-muted-foreground transition-transform ${isExpanded ? "rotate-180" : ""
                      }`}
                  />
                </button>

                {/* Card Content */}
                <AnimatePresence>
                  {isExpanded && (
                    <motion.div
                      initial={{ height: 0, opacity: 0 }}
                      animate={{ height: "auto", opacity: 1 }}
                      exit={{ height: 0, opacity: 0 }}
                      transition={{ duration: 0.3 }}
                      className="overflow-hidden"
                    >
                      <div className="p-6 pt-0 space-y-6">
                        {sections.map((section) => (
                          <div key={section.id} className="space-y-3">
                            <div className="flex items-center justify-between">
                              <h4 className="text-lg font-semibold text-foreground">
                                {section.title}
                              </h4>
                              <div className="flex items-center gap-2">
                                <button
                                  onClick={() => copyToClipboard(section.content || "", `${character.id}-${section.id}`)}
                                  className="px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors flex items-center gap-2 text-sm"
                                >
                                  {copied === `${character.id}-${section.id}` ? (
                                    <>
                                      <CheckCircle2 className="w-4 h-4 text-green-400" />
                                      Copied!
                                    </>
                                  ) : (
                                    <>
                                      <Copy className="w-4 h-4" />
                                      Copy
                                    </>
                                  )}
                                </button>
                                <button
                                  className="px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors flex items-center gap-2 text-sm"
                                  title="Rewrite (UI only)"
                                >
                                  <RefreshCw className="w-4 h-4" />
                                  Rewrite
                                </button>
                              </div>
                            </div>
                            <textarea
                              value={section.content}
                              onChange={() => { }}
                              className="w-full h-32 px-4 py-3 rounded-xl glass border border-border focus:border-violet-500 transition-all outline-none resize-none text-sm"
                            />
                          </div>
                        ))}
                      </div>
                    </motion.div>
                  )}
                </AnimatePresence>
              </motion.div>
            );
          })}
        </div>

        {/* Actions */}
        <div className="mt-8 flex gap-4">
          <Link
            href="/create"
            className="flex-1 py-4 rounded-2xl glass border border-border hover:bg-white/5 transition-colors text-center font-medium"
          >
            Create More
          </Link>
          <Link
            href="/wizard"
            className="flex-1 py-4 rounded-2xl bg-violet-500 hover:bg-violet-600 text-white font-semibold transition-colors text-center"
          >
            Edit Characters
          </Link>
        </div>
      </div>
    </div>
  );
}
